"use client";

import { useState } from 'react';
import useSWR from 'swr';
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';
import { Send, Loader2 } from 'lucide-react';

const fetcher = (url: string) => fetch(url).then(res => res.json());

export default function GuestbookPage() {
    const { data: messages, mutate } = useSWR('/api/guestbook', fetcher);
    // Since this is a client page, we can't easily pass settings to Navbar unless we fetch them or layout passes them.
    // For now we'll assume Navbar can fetch its own or we ignore resume link here for simplicity, 
    // OR we fetch settings here too. Let's fetch settings here too to keep it consistent.
    const { data: settings } = useSWR('/api/settings', fetcher);

    const [form, setForm] = useState({ name: '', message: '' });
    const [loading, setLoading] = useState(false);
    const [success, setSuccess] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);

        try {
            const res = await fetch('/api/guestbook', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(form)
            });

            if (res.ok) {
                setForm({ name: '', message: '' });
                setSuccess(true);
                setTimeout(() => setSuccess(false), 5000);
                // We don't mutate immediately because it needs approval
            }
        } catch (error) {
            console.error(error);
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="bg-background min-h-screen text-secondary font-sans selection:bg-accent selection:text-white flex flex-col">
            <Navbar settings={settings} />

            <main className="flex-grow py-24 px-6">
                <div className="max-w-2xl mx-auto">
                    <div className="text-center mb-16">
                        <h1 className="font-display text-4xl md:text-5xl font-bold text-white mb-6">
                            Guest<span className="text-accent">book</span>
                        </h1>
                        <p className="text-lg text-secondary">
                            Leave a note, say hi, or drop a suggestion.
                        </p>
                    </div>

                    <div className="bg-surface border border-white/10 rounded-2xl p-6 md:p-8 mb-16">
                        <h3 className="text-xl font-bold text-white mb-6">Sign the Guestbook</h3>
                        <form onSubmit={handleSubmit} className="space-y-4">
                            <div>
                                <input
                                    placeholder="Your Name"
                                    value={form.name}
                                    onChange={e => setForm({ ...form, name: e.target.value })}
                                    className="w-full bg-surfaceHighlight border border-border rounded p-3 text-primary focus:outline-none focus:border-accent"
                                    required
                                    maxLength={50}
                                />
                            </div>
                            <div>
                                <textarea
                                    placeholder="Your Message..."
                                    value={form.message}
                                    onChange={e => setForm({ ...form, message: e.target.value })}
                                    className="w-full bg-surfaceHighlight border border-border rounded p-3 text-primary focus:outline-none focus:border-accent"
                                    rows={3}
                                    required
                                    maxLength={500}
                                />
                            </div>

                            <div className="flex items-center justify-between">
                                {success && <span className="text-green-500 text-sm">Message sent for approval!</span>}
                                {!success && <span></span>}

                                <button
                                    type="submit"
                                    disabled={loading}
                                    className="flex items-center gap-2 px-6 py-2 bg-white text-black font-semibold rounded hover:bg-gray-200 transition-colors disabled:opacity-50"
                                >
                                    {loading ? <Loader2 className="animate-spin" size={18} /> : <Send size={18} />}
                                    Sign
                                </button>
                            </div>
                        </form>
                    </div>

                    <div className="space-y-6">
                        {messages?.map((msg: any) => (
                            <div key={msg._id} className="border-b border-white/5 pb-6 last:border-0">
                                <div className="flex items-baseline justify-between mb-2">
                                    <h4 className="text-white font-bold">{msg.name}</h4>
                                    <span className="text-xs text-secondary/50">{new Date(msg.createdAt).toLocaleDateString()}</span>
                                </div>
                                <p className="text-secondary leading-relaxed">{msg.message}</p>
                            </div>
                        ))}
                        {messages?.length === 0 && (
                            <div className="text-center text-secondary py-12">
                                No approved messages yet. Be the first!
                            </div>
                        )}
                    </div>
                </div>
            </main>

            <Footer />
        </div>
    );
}
